import'./createPost.css';
import'./styles.css';
import { useState, useContext, createContext} from 'react'
import { GlobalContext } from '../../../../App';
import { celesupApi } from '../../../../axiosInstances'
import PostForm from './postForm'; 

export const PostContext = createContext({})

const CreateNewPost = () => {
	
	const [pending, setPending] = useState(false)
	const [error, setError] = useState(null)

	const context = useContext(GlobalContext)
	const [formData, updateFormData] = useState({caption: '', excerpt: '', hashtags: '', picture: null })

	async function submitForm() {		
		if (formData.caption === '' && formData.picture === null) return

		setPending(prv=>!prv)

		const form = new FormData()
		form.append("caption", formData.caption)
		// form.append("excerpt", formData.excerpt)
		form.append("hashtags", formData.hashtags)
		form.append("picture", formData.picture)

		celesupApi
			.post('posts/create', form, {
				headers: {'Content-Type': 'application/json'}
			})
			.then(res=>{
				if (res.data){
					context.getPosts()
					setPending(prv=>!prv)
					context.setCreatePost(prev=>!prev)
				}
			},
			(err)=>{
				setError(err)
				setPending(prv=>!prv)
				console.log(err);
			}

			)
			.catch((error)=>{
				setError(error)
				setPending(prv=>!prv)
				console.log(error);
			})
	}

	return (
		<div className="">
			<PostContext.Provider value={{formData, updateFormData, submitForm, pending, error}}>
				<PostForm />
			</PostContext.Provider>
		</div>
	)
}

export default CreateNewPost
