import { useState } from "react"

const useAxiosRequest = () => {
    const [response, setResponse] = useState(null)
    const [pending, setPending] = useState(false)
    const [error, setError] = useState(null)

    async function sendAxiosRequest(configuration){
        const {axiosInstance, url, method, form, options} = configuration
        setPending(true)
        switch (method.toLowerCase()) {
            case 'get':
                axiosInstance[method.toLowerCase()](url)
                    .then((res)=>{
                        setResponse(res.data)
                        setPending(false)
                    },(error)=>{
                        const errorMsg = error.response.data?.detail || error.response.data?.email || error.response.data?.avatar || error.message
                        setError(errorMsg)
                        setPending(false)
                    })
                return;
            
            case 'post' || 'put' || 'delete':
                axiosInstance[method.toLowerCase()](url, form, options )
                    .then((res)=>{
                        setResponse(res.data)
                        setPending(false)
                    },(error)=>{
                        const errorMsg = error.response.data?.detail || error.response.data?.email || error.response.data?.avatar || error.message
                        setError(errorMsg)
                        setPending(false)
                    })                    
                return;
            
                
            default:
                break;
        }
    }

    return [response, pending, error, sendAxiosRequest]
}

export default useAxiosRequest
